import uniRate from "@/components/uni-rate/uni-rate.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default{
	components: {uniRate,uniPopup},
	data(){
		return{
			id:0,
			isShow:false,
			assessBox:"",
			assessInfo:"",
			img:"",
			shopObj:"",
		}
	},
	methods:{
		isShowChange(){
			this.isShow=!this.isShow;
			this.assessBox="";
		},
		ok(){
			let that=this;
			if(that.assessBox){
				this.$ajax({
					url:"/memberManager/memberManagerCommentReply",
					method:"POST",
					data:{
						evalId: that.assessInfo.evalId,
						reply: that.assessBox,
						shopId: that.shopObj.shopId
					},
					success(d){
						that.assessInfo.reply=that.assessBox;
						that.isShow=!that.isShow;
						that.assessInfo.replyStatus=2
					}
				})
			}else{
				uni.showToast({
					title:"请输入要回复的内容",
					icon:"none"
				})
			}
		}
	},
	onLoad(e){
		let that=this;
		that.id=e.id;
		that.img=e.img;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
		that.$ajax({
			url:"/order/showOrderEvaluate",
			data:{
				orderLineId:e.id
			},success(d){
				
				if(d&&d.imgs){
					d.imgs=d.imgs.split(",")
				}
				that.assessInfo=d;
			}
		})
	}
}
